import { Component, OnInit, NgModule } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { texts } from './texts';
import { FormControl } from '@angular/forms';
import { MatInput } from '@angular/material/input';
import { MatCardTitle, MatCardContent, MatCardHeader } from '@angular/material/card';
import { MatFormField } from '@angular/material/form-field';
import { MatSelect } from '@angular/material/select';
import { TdDialogService } from '@covalent/core/dialogs';
import {
  TdDataTableColumnComponent,
} from '@covalent/core/data-table';
import { CompetitionService } from '../service/competition.service';
import { Competition } from '../models/competition';
import { TdPagingBarComponent } from '@covalent/core/paging';
import { Pavilion } from '../models/pavilion';


@NgModule({
  declarations: [
    FormControl,
    MatInput,
    MatCardTitle,
    MatCardContent,
    MatCardHeader,
    MatFormField,
    MatSelect,
    TdDialogService,
    TdDataTableColumnComponent,
    TdPagingBarComponent,
  ],
})


@Component({
  selector: 'app-view-competition',
  templateUrl: './view-competition.component.html',
  styleUrls: ['./view-competition.component.css']
})
export class ViewCompetition implements OnInit {

  competition: Competition;
  texts = texts;
  moment: Date;
  hour: string;
  score: string;
  description: string;
  name: string;
  id: number;
  pavilion: Pavilion;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private toastr: ToastrService,
    private competitionService: CompetitionService) { }


  ngOnInit() {
    this.activatedRoute.queryParams.subscribe(params => {
      this.id = params['id'];
    });

    this.getCompetition();
  }

  getCompetition(): void {
    this.getCompetitionFromService().then((competition: Competition) => {
      competition !== null ? this.setResult(competition) : this.showError(texts.errorDataCompetition);
    });
  }

  private setResult(competition: Competition): void {
    this.moment = competition.moment;
    this.hour = competition.hour;
    this.score = competition.score;
    this.description = competition.description;
    this.name = competition.name;
    this.pavilion = competition.pavilion;

  }

  private async getCompetitionFromService(): Promise<Competition> {
    try {
      return await this.competitionService.get(this.id).toPromise();
    } catch (error) {

      throw error;
    }
  }

  private showError(message: string): void {
    this.toastr.success(message, 'ERROR', {
      timeOut: 3000, positionClass: 'toast-top-right'
    });
  }


}
