import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { TokenService } from '../service/token.service';

@Injectable({
  providedIn: 'root'
})
export class  GuardService implements CanActivate {



  constructor(
    private tokenService: TokenService,
    private router: Router
  ) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    const expectedRol = route.data.expectedRol;
    const roles = this.tokenService.getAuthorities();
    let realRol = false;
    //TODO: aleromlop cambiar foreach para poner break
    roles.forEach(rol => {
      if (this.roleFound(expectedRol, rol)) {
        realRol = true;
      }
    });
    if (!this.tokenService.getToken() || realRol === false) {
      this.router.navigate(['/']);
      return false;
    }
    return true;
  }

  roleFound(expectedRol: string, rol: string): boolean {

    return expectedRol.includes(rol);
  }
}
