import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ConfigService } from './config.service';
import { Competition } from '../models/competition';
import { Pagination, IPaginationPage } from '../models/pagination';


@Injectable({
  providedIn: 'root'
})
export class CompetitionService {

  private baseURL = this.configService.getBasePath() + '/competition/';

  constructor(private httpClient: HttpClient, private configService: ConfigService) { }

  public competitions(pagination: Pagination): Observable<IPaginationPage<Competition>> {

    const parameters = 'page=' + pagination.page + '&size=' + pagination.size + '&sort=' +
          pagination.sortField + ',' + pagination.sortOrder.toLowerCase();


    return this.httpClient.get<IPaginationPage<Competition>>(this.baseURL + 'competitions?' + parameters);
  }

  public get(id: number): Observable<Competition> {
    return this.httpClient.get<Competition>(this.baseURL + 'view?id=' + id);
  }

}

