import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { WelcomeComponent } from './welcome/welcome.component';
import { EditAssistant } from './edit-assistant/edit-assistant.component';
import { GuardService } from './guards/guard.service';
import { EditUserAccount } from './edit-userAccount/edit-userAccount.component';
import { ListCompetition } from './list-competition/list-competition.component';
import { ViewCompetition } from './view-competition/view-competition.component';
import { ViewPavilion } from './view-pavilion/view-pavilion.component';
import { ListPool } from './list-pool/list-pool.component';
import { ViewPool } from './view-pool/view-pool.component';


const ROLE_ADMIN      = 'ADMIN';
const ROLE_ASSISTANT  = 'ASSISTANT';
const ROLE_FEDERATION = 'FEDERATION';
const ROLE_SWIMMER    = 'SWIMMER';
const ROLE_JUDGE      = 'JUDGE';

const routes: Routes = [
  {
    path: '',
    component: WelcomeComponent,
  },
  {
    path: '',
    children: [
      {
        component: LoginComponent,
        path: 'login',
      },
      {
        component: EditAssistant,
        path: 'register-assistant',
      },
      {
        component: EditAssistant,
        path: 'edit-assistant',
        canActivate: [GuardService], data: { expectedRol: [ROLE_ASSISTANT] }
      },
      {
        component: EditUserAccount,
        path: 'edit-userAccount',
        canActivate: [GuardService], data: { expectedRol: [ROLE_ASSISTANT] }
      },
      {
        component: ListCompetition,
        path: 'list-competition',
      },
      {
        component: ViewCompetition,
        path: 'view-competition',
      },
      {
        component: ViewPavilion,
        path: 'view-pavilion',
      },
      {
        component: ListPool,
        path: 'list-pool',
      },
      {
        component: ViewPool,
        path: 'view-pool',
      },
    ]
  },
  // otherwise redirect to home
  { path: '**', redirectTo: '' }
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class appRoutes { }
