import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ConfigService } from './config.service';
import { Pagination, IPaginationPage } from '../models/pagination';
import { Pool } from '../models/pool';


@Injectable({
  providedIn: 'root'
})
export class PoolService {

  private baseURL = this.configService.getBasePath() + '/pool/';

  constructor(private httpClient: HttpClient, private configService: ConfigService) { }

  public pools(pagination: Pagination, idPavilion: number): Observable<IPaginationPage<Pool>> {

    const parameters = 'idPavilion=' + idPavilion + '&page=' + pagination.page + '&size=' + pagination.size + '&sort=' +
          pagination.sortField + ',' + pagination.sortOrder.toLowerCase();


    return this.httpClient.get<IPaginationPage<Pool>>(this.baseURL + 'pools?' + parameters);
  }

  public get(id: number): Observable<Pool> {
    return this.httpClient.get<Pool>(this.baseURL + 'view?id=' + id);
  }

}

