import { Component, OnInit, NgModule } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { texts } from './texts';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import { MatInput } from '@angular/material/input';
import { MatCardTitle, MatCardContent, MatCardHeader } from '@angular/material/card';
import { editPasswordValidator } from '../validators/actor.validators';
import { UserAccount } from '../models/userAccount';
import { UserAccountService } from '../service/userAccount.service';
import { TokenService } from '../service/token.service';
import { MatFormField } from '@angular/material/form-field';
import { MatSelect } from '@angular/material/select';
import { TdDialogService } from '@covalent/core/dialogs';

@NgModule({
  declarations: [
    FormControl,
    MatInput,
    MatCardTitle,
    MatCardContent,
    MatCardHeader,
    MatFormField,
    MatSelect,
    TdDialogService,
    FormGroup,
  ],
})

@Component({
  selector: 'app-edit-userAccount',
  templateUrl: './edit-userAccount.component.html',
  styleUrls: ['./edit-userAccount.component.css']
})
export class EditUserAccount implements OnInit {

  userAccount: UserAccount;
  groupPassword: FormGroup = new FormGroup({
    username: new FormControl(''),
    passwordNew: new FormControl(''),
    passwordOld: new FormControl(''),
    confirmPassword: new FormControl('')
  }, { validators: [Validators.required, editPasswordValidator] });

  texts = texts;
  errorMessage: string;

  constructor(
    private router: Router,
    private toastr: ToastrService,
    private userAccountService: UserAccountService,
    private dialogService: TdDialogService,
    private tokenService: TokenService) { }

  ngOnInit() {
    this.getUsername();
  }

  isDisabled(): boolean {
    return this.formValid() ? false : true;
  }

  editAction(): void {
    if (this.formValid()) {
      this.buildUserAccount();
      this.edit();
    }
  }

  cancel(): void {
    this.router.navigate(['']);
  }

  private getUsername(): void {
    this.groupPassword.controls.username.setValue(this.tokenService.getUserName());
  }

  private formValid(): boolean {
    return this.formValidEditUserAccount();
  }

  private formValidEditUserAccount(): boolean {
    if (this.groupPassword.valid) {
      return true;
    } else {
      return false;
    }
  }

  private buildUserAccount(): void {
    this.userAccount = new UserAccount(this.groupPassword.controls.username.value,
      this.groupPassword.controls.passwordNew.value,
      this.groupPassword.controls.passwordOld.value);
  }

  private showError(message: string): void {
    this.toastr.error(message, 'ERROR', {
      timeOut: 3000, positionClass: 'toast-top-right'
    });
  }

  private showSuccess(message: string): void {
    this.toastr.success(message, 'SUCCESS', {
      timeOut: 3000, positionClass: 'toast-top-center'
    });
  }

  private edit(): void {
    this.dialogService.openConfirm({
      message: texts.confirmMessageSave,
      title: texts.titleEdit,
      acceptButton: texts.confirm,
      cancelButton: texts.cancel
    }).afterClosed().subscribe((accept: boolean) => {
      if (accept) {
        this.userAccountService.edit(this.userAccount).subscribe(
          data => {
            this.showSuccess(this.texts.userAccountSuccessfullyEdited);
            this.router.navigate(['/']);
          },
          err => {
            this.errorMessage = err.error.message;
            this.showError(this.errorMessage);
          }
        );
      }
    });
  }
}
