import { Component, OnInit, NgModule } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { texts } from './texts';
import { FormControl } from '@angular/forms';
import { MatInput } from '@angular/material/input';
import { MatCardTitle, MatCardContent, MatCardHeader } from '@angular/material/card';
import { MatFormField } from '@angular/material/form-field';
import { MatSelect } from '@angular/material/select';
import { TdDialogService } from '@covalent/core/dialogs';
import {
  TdDataTableColumnComponent,
} from '@covalent/core/data-table';
import { PavilionService } from '../service/pavilion.service';
import { TdPagingBarComponent } from '@covalent/core/paging';
import { Pavilion } from '../models/pavilion';


@NgModule({
  declarations: [
    FormControl,
    MatInput,
    MatCardTitle,
    MatCardContent,
    MatCardHeader,
    MatFormField,
    MatSelect,
    TdDialogService,
    TdDataTableColumnComponent,
    TdPagingBarComponent,
  ],
})


@Component({
  selector: 'app-view-pavilion',
  templateUrl: './view-pavilion.component.html',
  styleUrls: ['./view-pavilion.component.css']
})
export class ViewPavilion implements OnInit {


  id: number;
  code: string;
  name: string;
  address: string;
  capacity: string;
  texts = texts;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private toastr: ToastrService,
    private pavilionService: PavilionService) { }

  ngOnInit() {
    this.activatedRoute.queryParams.subscribe(params => {
      this.id = params['id'];
    });

    this.getPavilion();
  }

  getPavilion(): void {
    this.getPavilionFromService().then((pavilion: Pavilion) => {
      pavilion !== null ? this.setResult(pavilion) : this.showError(texts.errorDataPavilion);
    });
  }

  private setResult(pavilion: Pavilion): void {
    this.id = pavilion.id;
    this.code = pavilion.code;
    this.name = pavilion.name;
    this.address = pavilion.address;
    this.capacity = pavilion.capacity;

  }

  private async getPavilionFromService(): Promise<Pavilion> {
    try {
      return await this.pavilionService.get(this.id).toPromise();
    } catch (error) {

      throw error;
    }
  }

  private showError(message: string): void {
    this.toastr.success(message, 'ERROR', {
      timeOut: 3000, positionClass: 'toast-top-right'
    });
  }


}
