import { UserAccount } from './userAccount';


export class Actor {
   name: string;
   surnames: string;
   email: string;
   phoneNumber: string;
   address: string;
   dateOfBirth: Date;
   nif: string;
   province: string;
   population: string;
   postalCode: string;
   userAccount: UserAccount;
   imageBase64: string;

  constructor(name: string, surnames: string, email: string, phoneNumber: string,
              address: string, dateOfBirth: Date, nif: string, province: string,
              population: string, postalCode: string, userAccount: UserAccount, imageBase64: string) {

    this.name = name;
    this.surnames = surnames;
    this.email = email;
    this.phoneNumber = phoneNumber;
    this.address = address;
    this.dateOfBirth = dateOfBirth;
    this.nif = nif;
    this.province = province;
    this.population = population;
    this.postalCode = postalCode;
    this.userAccount = userAccount;
    this.imageBase64 = imageBase64;

  }

}
