import { Component, OnInit, NgModule } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { texts } from './texts';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import { MatInput } from '@angular/material/input';
import { MatCardTitle, MatCardContent, MatCardHeader } from '@angular/material/card';
import { nifValidator, emailValidator, postalCodeValidator, passwordValidator } from '../validators/actor.validators';
import { Assistant } from '../models/assistant';
import { UserAccount } from '../models/userAccount';
import { AssistantService } from '../service/assistant.service';
import { MatFormField } from '@angular/material/form-field';
import { MatSelect } from '@angular/material/select';
import { Utilities } from '../utilities/utilities';
import { TdDialogService } from '@covalent/core/dialogs';
import { Population } from '../models/population';
import { ConfigService } from '../service/config.service';

@NgModule({
  declarations: [
    FormControl,
    MatInput,
    MatCardTitle,
    MatCardContent,
    MatCardHeader,
    MatFormField,
    MatSelect,
    TdDialogService,
    FormGroup,

  ],
})


@Component({
  selector: 'app-edit-assistant',
  templateUrl: './edit-assistant.component.html',
  styleUrls: ['./edit-assitant.component.css']
})
export class EditAssistant implements OnInit {

  assistant: Assistant;
  userAccount: UserAccount;
  groupProfile: FormGroup = new FormGroup({
    name: new FormControl(''),
    surnames: new FormControl(''),
    province: new FormControl('', { validators: [Validators.required] }),
    population: new FormControl('', { validators: [Validators.required] }),
    postalCode: new FormControl('', { validators: [postalCodeValidator] }),
    email: new FormControl('', { validators: [emailValidator] }),
    address: new FormControl(''),
    phoneNumber: new FormControl(''),
    nif: new FormControl('', { validators: [nifValidator] }),
    dateOfBirth: new FormControl(''),
  }, { validators: [Validators.required] });

  groupPassword: FormGroup = new FormGroup({
    username: new FormControl(''),
    password: new FormControl(''),
    confirmPassword: new FormControl('')
  }, { validators: [Validators.required, passwordValidator] });

  texts = texts;
  errorMessage: string;
  provinces: Array<{ code: string, name: string }>;
  populations: Array<Population>;
  isEditUser: boolean;

  imageBase64: string;


  constructor(
    private router: Router,
    private toastr: ToastrService,
    private assistanService: AssistantService,
    private utilities: Utilities,
    private activatedRoute: ActivatedRoute,
    private dialogService: TdDialogService,
    private configService: ConfigService) { }

  ngOnInit() {
    this.isEdit();
    this.setProvinces();

    if (this.isEditUser) {
      this.getUser();
    }

  }

  isDisabled(): boolean {
    return this.formValid() ? false : true;
  }

  isDisabledPopulation(): boolean {
    return this.groupProfile.controls.province.valid ? false : true;
  }

  registerAction(): void {
    if (this.formValid()) {
      this.buildAssistant();
      this.register();
    }
  }

  editAction(): void {
    if (this.formValid()) {
      this.buildAssistant();
      this.edit();
    }
  }

  loadPopulations(): void {
    const province = this.provinces.find(element => element.name === this.groupProfile.controls.province.value);

    if (province !== undefined) {
      this.setPopulations(province.code);
    }
  }

  cancel(): void {
    this.router.navigate(['']);
  }

  getUser(): void {
    this.getUserFromService().then((assistant: Assistant) => {
      assistant !== null ? this.setResult(assistant) : this.showError(texts.errorDataAssistant);
    });
  }

  preview(files: any) {

    if (files[0].size > this.configService.getImgSizeMax()) {
      this.showError(texts.errorImgSizeMax + this.utilities.formatBytes(this.configService.getImgSizeMax(), 1));

      return;
    }

    const mimeType = files[0].type;

    if (mimeType.match(/image\/*/) == null) {
      this.showError(texts.errorOnlyImageSupported);

      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(files[0]);
    reader.onload = (e) => {
      this.imageBase64 = reader.result.toString().split(',')[1];
    };

  }

  private setResult(assistant: Assistant): void {
    this.groupProfile.controls.name.setValue(assistant.name);
    this.groupProfile.controls.surnames.setValue(assistant.surnames);
    this.groupProfile.controls.address.setValue(assistant.address);
    this.groupProfile.controls.phoneNumber.setValue(assistant.phoneNumber);
    this.groupProfile.controls.nif.setValue(assistant.nif);
    this.groupProfile.controls.dateOfBirth.setValue(assistant.dateOfBirth);
    this.groupPassword.controls.username.setValue(assistant.userAccount.username);
    this.groupProfile.controls.postalCode.setValue(assistant.postalCode);
    this.groupProfile.controls.province.setValue(assistant.province);
    this.loadPopulations();
    this.groupProfile.controls.population.setValue(assistant.population);
    this.groupProfile.controls.email.setValue(assistant.email);
    this.imageBase64 = assistant.imageBase64;

  }
  private async getUserFromService(): Promise<Assistant> {
    try {
      return await this.assistanService.get().toPromise();
    } catch (error) {

      throw error;
    }
  }

  private isEdit(): void {

    this.activatedRoute.queryParams.subscribe(params => {
      params['edit'] === "true" ? this.isEditUser = true : this.isEditUser = false;
    });

  }

  private setProvinces(): void {
    this.provinces = this.utilities.getProvinces();
  }

  private setPopulations(provinceCode: string): void {
    this.utilities.getPopulationsFromAPI(provinceCode)
      .subscribe(
        (data) => {
          this.populations = data['municipios'];
        },
        (error) => {
          this.errorMessage = texts.errorNoDataPopulationAPI;
          this.showError(this.errorMessage);
        }
      );
  }

  private formValid(): boolean {
    return this.isEditUser ? this.formValidEditAssistant() : this.formValidRegister();
  }

  private formValidRegister(): boolean {
    if (this.groupProfile.valid && this.groupPassword.valid) {
      return true;
    } else {
      return false;
    }
  }

  private formValidEditAssistant(): boolean {
    if (this.groupProfile.valid) {
      return true;
    } else {
      return false;
    }
  }

  private buildAssistant(): void {
    this.userAccount = new UserAccount(this.groupPassword.controls.username.value,
      this.groupPassword.controls.password.value, null);
    this.assistant = new Assistant(this.groupProfile.controls.name.value,
      this.groupProfile.controls.surnames.value, this.groupProfile.controls.email.value,
      this.groupProfile.controls.phoneNumber.value, this.groupProfile.controls.address.value,
      this.groupProfile.controls.dateOfBirth.value, this.groupProfile.controls.nif.value,
      this.groupProfile.controls.province.value, this.groupProfile.controls.population.value,
      this.groupProfile.controls.postalCode.value, this.userAccount, this.imageBase64);
  }

  private register(): void {
    this.dialogService.openConfirm({
      message: texts.confirmMessageSave,
      title: texts.titleRegedit,
      acceptButton: texts.confirm,
      cancelButton: texts.cancel
    }).afterClosed().subscribe((accept: boolean) => {
      if (accept) {
        this.assistanService.register(this.assistant).subscribe(
          data => {
            this.showSuccess(this.texts.userSuccessfullyCreated);
            this.router.navigate(['/login']);
          },
          err => {
            this.errorMessage = err.error.message;
            this.showError(this.errorMessage);
          }
        );
      }
    });
  }

  private showError(message: string): void {
    this.toastr.success(message, 'ERROR', {
      timeOut: 3000, positionClass: 'toast-top-right'
    });
  }

  private showSuccess(message: string): void {
    this.toastr.success(message, 'SUCCESS', {
      timeOut: 3000, positionClass: 'toast-top-center'
    });
  }

  private edit(): void {
    this.dialogService.openConfirm({
      message: texts.confirmMessageSave,
      title: texts.titleEdit,
      acceptButton: texts.confirm,
      cancelButton: texts.cancel
    }).afterClosed().subscribe((accept: boolean) => {
      if (accept) {
        this.assistanService.edit(this.assistant).subscribe(
          data => {
            this.showSuccess(this.texts.userSuccessfullyEdited);
            this.router.navigate(['/edit-assistant']);
          },
          err => {
            this.errorMessage = err.error.message;
            this.showError(this.errorMessage);
          }
        );
      }
    });
  }
}
