import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Assistant } from '../models/assistant';
import { ConfigService } from './config.service';
import { Message } from '../models/message';


@Injectable({
  providedIn: 'root'
})
export class AssistantService {

  private baseURL = this.configService.getBasePath() + '/assistant/';

  constructor(private httpClient: HttpClient, private configService: ConfigService) { }

  public register(assistant: Assistant): Observable<any> {
    return this.httpClient.post<Message>(this.baseURL + 'register', assistant);
  }

  public get(): Observable<Assistant> {
    return this.httpClient.get<Assistant>(this.baseURL + 'assistant/get');
  }

  public edit(assistant: Assistant): Observable<any> {
    return this.httpClient.post<Message>(this.baseURL + 'assistant/edit', assistant);
  }
}

