import { Component, OnInit, NgModule } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { texts } from './texts';
import { FormControl, Validators } from '@angular/forms';
import { MatInput } from '@angular/material/input';
import { MatCardTitle, MatCardContent, MatCardHeader } from '@angular/material/card';
import { MatFormField } from '@angular/material/form-field';
import { MatSelect } from '@angular/material/select';
import { TdDialogService } from '@covalent/core/dialogs';
import {
  TdDataTableColumnComponent,
  TdDataTableSortingOrder, ITdDataTableColumn, ITdDataTableSortChangeEvent
} from '@covalent/core/data-table';
import { CompetitionService } from '../service/competition.service';
import { Competition } from '../models/competition';
import { Pagination } from '../models/pagination';
import { TdPagingBarComponent, IPageChangeEvent } from '@covalent/core/paging';






@NgModule({
  declarations: [
    FormControl,
    MatInput,
    MatCardTitle,
    MatCardContent,
    MatCardHeader,
    MatFormField,
    MatSelect,
    TdDialogService,
    TdDataTableColumnComponent,
    TdPagingBarComponent,
  ],
})


@Component({
  selector: 'app-list-competition',
  templateUrl: './list-competition.component.html',
  styleUrls: ['./list-competition.component.css']
})
export class ListCompetition implements OnInit {

  competitions: Competition[] = new Array();
  texts = texts;
  columns: ITdDataTableColumn[] = [
    { name: 'id', label: texts.id, width: { min: 5, max: 500 } },
    { name: 'moment', label: texts.moment, width: { min: 5, max: 500 } },
    { name: 'hour', label: texts.hour, width: { min: 5, max: 500 } },
    { name: 'score', label: texts.score, width: { min: 5, max: 500 } },
    { name: 'description', label: texts.description, width: { min: 5, max: 500 } },

  ];
  pagination: Pagination;
  sortOrder: TdDataTableSortingOrder;
  competitionsTotal: number;



  constructor(
    private router: Router,
    private toastr: ToastrService,
    private competitionServie: CompetitionService) { }


  ngOnInit() {
    this.initPagination();
    this.setCompetitions();
  }

  sortEvent(sortEvent: ITdDataTableSortChangeEvent): void {
    if (this.competitions.length !== 0) {
      this.sortOrder = (this.sortOrder === TdDataTableSortingOrder.Ascending)
        ? TdDataTableSortingOrder.Descending : TdDataTableSortingOrder.Ascending;
      this.pagination.sortOrder = sortEvent.order;
      this.pagination.sortField = sortEvent.name;
      this.setCompetitions();
    }
  }

  page(pagingEvent: IPageChangeEvent): void {
    this.pagination.size = pagingEvent.pageSize;
    this.pagination.page = pagingEvent.page - 1;
    this.setCompetitions();
  }


  private setCompetitions(): void {
    this.competitionServie.competitions(this.pagination).subscribe(
      data => {
        this.competitions = data.content;
        this.competitionsTotal = data.totalElements;
        this.pagination.page = data.number;
        this.pagination.size = data.size;
      },
      err => {
        console.log(err);
      }
    );
  }

  private initPagination(): void {
    this.pagination = { size: 10, page: 0, sortField: 'moment', sortOrder: 'DESC', };
    this.competitionsTotal = 0;
  }

}
