import { Injectable } from '@angular/core';

const BASE_PATH = 'http://localhost:8080';
const IMG_SIZE_MAX_BYTE = 5242880;

@Injectable({
  providedIn: 'root'
})
export class ConfigService {
  constructor() {

  }
  getBasePath(): string {
    return BASE_PATH;
  }

  getImgSizeMax(): number {
    return IMG_SIZE_MAX_BYTE;
  }
}
