import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TokenService } from '../service/token.service';
import { texts } from './texts'

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  isLogged = false;
  texts = texts;

  constructor(private router: Router, private tokenService: TokenService) { }

  ngOnInit() {
    this.initIsLogged();
  }

  getIsLogged(): boolean {
    return this.tokenService.getToken() ? true : false;
  }

  redirectEditProfile(): void {
    this.router.navigate(['edit-assistant'], { queryParams: { edit: true}, skipLocationChange: true });
  }

  redirectEditPassword(): void {
    this.router.navigate(['edit-userAccount'], {skipLocationChange: true });
  }

  onLogOut(): void {
    this.tokenService.logOut();
    window.location.reload();
  }

  private navigateTo(route: string): void {
    this.router.navigate([route]);
  }

  private initIsLogged(): void {
    if (this.tokenService.getToken()) {
      this.isLogged = true;
    } else {
      this.isLogged = false;
    }
  }
}
