import { Component, OnInit, NgModule } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { texts } from './texts';
import { FormControl } from '@angular/forms';
import { MatInput } from '@angular/material/input';
import { MatCardTitle, MatCardContent, MatCardHeader } from '@angular/material/card';
import { MatFormField } from '@angular/material/form-field';
import { MatSelect } from '@angular/material/select';
import { TdDialogService } from '@covalent/core/dialogs';
import {
  TdDataTableColumnComponent,
} from '@covalent/core/data-table';
import { TdPagingBarComponent } from '@covalent/core/paging';
import { Pavilion } from '../models/pavilion';
import { Pool } from '../models/pool';
import { PoolService } from '../service/pool.service';


@NgModule({
  declarations: [
    FormControl,
    MatInput,
    MatCardTitle,
    MatCardContent,
    MatCardHeader,
    MatFormField,
    MatSelect,
    TdDialogService,
    TdDataTableColumnComponent,
    TdPagingBarComponent,
  ],
})


@Component({
  selector: 'app-view-pool',
  templateUrl: './view-pool.component.html',
  styleUrls: ['./view-pool.component.css']
})
export class ViewPool implements OnInit {


  id: number;
  code: string;
  isForCompetition: string;
  isForTraining: string;
  numOfStreet: number;
  pavilion: Pavilion;
  texts = texts;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private toastr: ToastrService,
    private poolService: PoolService) { }

  ngOnInit() {
    this.activatedRoute.queryParams.subscribe(params => {
      this.id = params['id'];
    });

    this.getPool();
  }

  getPool(): void {
    this.getPoolFromService().then((pool: Pool) => {
      pool !== null ? this.setResult(pool) : this.showError(texts.errorDataPool);
    });
  }

  private setResult(pool: Pool): void {
    this.id = pool.id;
    this.code = pool.code;
    this.isForCompetition = pool.isForCompetition ? "Si" : "No";
    this.isForTraining = pool.isForTraining ? "Si" : "No";
    this.numOfStreet = pool.numOfStreet;
    this.pavilion = pool.pavilion;

  }

  private async getPoolFromService(): Promise<Pool> {
    try {
      return await this.poolService.get(this.id).toPromise();
    } catch (error) {

      throw error;
    }
  }

  private showError(message: string): void {
    this.toastr.success(message, 'ERROR', {
      timeOut: 3000, positionClass: 'toast-top-right'
    });
  }


}
