import { Component, OnInit, NgModule } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { texts } from './texts';
import { FormControl } from '@angular/forms';
import { MatInput } from '@angular/material/input';
import { MatCardTitle, MatCardContent, MatCardHeader } from '@angular/material/card';
import { MatFormField } from '@angular/material/form-field';
import { MatSelect } from '@angular/material/select';
import { TdDialogService } from '@covalent/core/dialogs';
import {
  TdDataTableColumnComponent,
  TdDataTableSortingOrder, ITdDataTableColumn, ITdDataTableSortChangeEvent
} from '@covalent/core/data-table';
import { PoolService } from '../service/pool.service';
import { Pool } from '../models/pool';
import { Pagination } from '../models/pagination';
import { TdPagingBarComponent, IPageChangeEvent } from '@covalent/core/paging';

@NgModule({
  declarations: [
    FormControl,
    MatInput,
    MatCardTitle,
    MatCardContent,
    MatCardHeader,
    MatFormField,
    MatSelect,
    TdDialogService,
    TdDataTableColumnComponent,
    TdPagingBarComponent,
  ],
})

@Component({
  selector: 'app-list-pool',
  templateUrl: './list-pool.component.html',
  styleUrls: ['./list-pool.component.css']
})
export class ListPool implements OnInit {

  pools: Pool[] = new Array();
  texts = texts;
  columns: ITdDataTableColumn[] = [
    { name: 'id', label: texts.id, width: { min: 5, max: 500 } },
    { name: 'code', label: texts.code, width: { min: 5, max: 500 } },
    { name: 'isForCompetition', label: texts.isForCompetition, width: { min: 5, max: 500 } },
    { name: 'isForTraining', label: texts.isForTraining, width: { min: 5, max: 500 } },
    { name: 'pavilion', label: texts.pavilion, width: { min: 5, max: 500 } },
    { name: 'numOfStreet', label: texts.numOfStreet, width: { min: 5, max: 500 } },
  ];

  pagination: Pagination;
  sortOrder: TdDataTableSortingOrder;
  poolsTotal: number;
  idPavilion: number;


  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private toastr: ToastrService,
    private poolServie: PoolService) { }

  ngOnInit() {

    this.activatedRoute.queryParams.subscribe(params => {
      this.idPavilion = params['idPavilion'];
    });

    this.initPagination();
    this.setPools();
  }

  sortEvent(sortEvent: ITdDataTableSortChangeEvent): void {
    if (this.pools.length !== 0) {
      this.sortOrder = (this.sortOrder === TdDataTableSortingOrder.Ascending)
        ? TdDataTableSortingOrder.Descending : TdDataTableSortingOrder.Ascending;
      this.pagination.sortOrder = sortEvent.order;
      this.pagination.sortField = sortEvent.name;
      this.setPools();
    }
  }

  page(pagingEvent: IPageChangeEvent): void {
    this.pagination.size = pagingEvent.pageSize;
    this.pagination.page = pagingEvent.page - 1;
    this.setPools();
  }


  private setPools(): void {
    this.poolServie.pools(this.pagination, this.idPavilion).subscribe(
      data => {
        this.pools = data.content;
        this.poolsTotal = data.totalElements;
        this.pagination.page = data.number;
        this.pagination.size = data.size;
      },
      err => {
        console.log(err);
      }
    );
  }

  private initPagination(): void {
    this.pagination = { size: 10, page: 0, sortField: 'code', sortOrder: 'DESC', };
    this.poolsTotal = 0;
  }

}
